.project {
  max-width: 500px;
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
}

.project:hover {
  transform: translateY(-7px);
}

.project__head {
  display: flex;
  align-items: center;
  justify-content: center;
}

.project__description {
  margin-top: 1em;
  text-indent: 1.2em;
  line-height: 1.6em;
  text-align: start;
}

.project__image {
  margin-top: 2em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}
